.hero {
  position: relative;
  &__wrapper {
    z-index: 3;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    height: 100dvh;
    padding-bottom: 16px;
    @include screen(tablet) {
      padding-bottom: 40px;
    }
  }
  &__video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100dvh;
    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title {
    font-size: 30px;
    line-height: 30px;
    font-weight: 500;
    @include screen(tablet) {
      font-size: 50px;
      line-height: 50px;
    }
  }
  &__description {
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    margin-top: 10px;
    @include screen(tablet) {
      font-size: 16px;
      margin-top: 30px;
      line-height: 20.8px;
    }
  }
  &__button {
    margin-top: 40px;
    width: 100%;
    @include screen(tablet) {
      margin-top: 60px;
      max-width: 313px;
    }
  }
  &__content {
    background-color: $white;
    padding: 16px;
    border-radius: 10px;
    @include screen(tablet) {
      padding: 40px;
      max-width: 640px;
      width: 100%;
    }
  }
}