.systems {
  padding: 30px 0;
  @include screen(tablet) {
    padding: 60px 0;
  }
  &__heading {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    display: grid;
    gap: 16px;
    h2 {
      @include screen(tablet) {
        grid-column: span 7 / span 7;
      }
    }
    p {
      font-weight: 500;
      font-size: 16px;
      line-height: 22px;
      letter-spacing: -0.01em;
      @include screen(tablet) {
        font-size: 24px;
        grid-column-start: 8;
        line-height: 29px;
        grid-column: span 4 / span 4;
      }
    }
    @include screen(tablet) {
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__items {
    display: grid;
    margin-top: 30px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 8px;
    @include screen(tablet) {
      margin-top: 48px;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      gap: 32px;
    }
  }
  &__item {
    border-radius: 10px;
    position: relative;
    overflow: hidden;
  }
  &__bg {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 22px;
    font-weight: 500;
    line-height: 26px;
    letter-spacing: -0.01em;
    @include screen(tablet) {
      font-size: 28px;
      line-height: 33px;
    }
  }
  &__text {
    margin-top: 32px;
    font-size: 16px;
    line-height: 22px;
    font-weight: 500;
    letter-spacing: -0.03em;
    @include screen(tablet) {
      font-size: 15px;
      margin-top: 40px;
    }
  }
  &__content {
    background-color: $white;
    position: absolute;
    border-radius: 8px;
    padding: 16px;
    bottom: 8px;
    left: 8px;
    right: 8px;
    @include screen(tablet) {
      padding: 24px 17px 24px 24px;
      bottom: 24px;
      left: 24px;
      right: 24px;
    }
  }
}