.header {
  position: fixed;
  top: 8px;
  z-index: 10;
  width: 100%;
  &__wrapper {
    background-color: $white;
    border-radius: 10px;
    display: flex;
    padding: 8px 14px;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0 2px 4px 0 rgba($dark, 0.2);
    z-index: 4;
    position: relative;
    @include screen(laptop) {
      padding: 13px;
      justify-content: flex-start;
    }
  }
  &__mobmenu {
    display: none;
    &.active {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100dvh;
      display: block;
      background-color: $white;
      .header {
        &__location {
          display: flex;
        }
        &__menu {
          flex-direction: column;
          align-items: center;
          gap: 40px;
          padding: 123px 0 32px;
        }
        &__right,
        &__left {
          display: flex;
          flex-direction: column;
          align-items: center;
        }
        &__catalog,
        &__link {
          display: flex;
        }
        &__nav {
          gap: 40px;
          display: flex;
          flex-direction: column;
          align-items: center;
        }
      }
    }
  }
  &__logo {
    max-width: 76px;
    margin: 0 auto;
    @include screen(tablet) {
      margin: 0;
      margin-right: 24px;
    }
    @include screen(laptop) {
      max-width: 94px;
    }
    @include screen(desktop) {
      max-width: 100%;
    }
  }
  &__menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 9px 19px 9px 30px;
    gap: 45px;
    @include screen(tablet) {
      border-left: 1px solid rgba($dark, 0.2);
      border-right: 1px solid rgba($dark, 0.2);
    }
    @include screen(desktop) {
      gap: 82px;
    }
  }
  &__left {
    display: none;
    @include screen(tablet) {
      display: flex;
      align-items: center;
    }
  }
  &__right {
    display: none;
    @include screen(tablet) {
      padding-left: 20px;
      display: flex;
      gap: 20px;
      align-items: center;
      flex: 1;
    }
    @include screen(desktop) {
      gap: 30px;
    }
  }
  &__location {
    display: none;
    gap: 8px;
    margin-top: 32px;
    span {
      font-size: 22px;
      padding-bottom: 10px;
      text-transform: uppercase;
      font-weight: 500;
      line-height: 20px;
      border-bottom: 1px dashed $dark;
      @include screen(laptop) {
        font-size: 13px;
        padding-bottom: 0;
      }
      @include screen(desktop) {
        font-size: 14px;
      }
    }
    @include screen(tablet) {
      margin-top: 0;
      display: flex;
      align-items: center;
      gap: 6px;
    }
  }
  &__button {
    justify-content: center;
    display: none;
    @include screen(laptop) {
      display: flex;
    }
    span {
      padding: 12px 19px;
      display: flex;
      font-weight: 600;
      font-size: 13px;
      @include screen(desktop) {
        padding: 17px 24px;
      }
    }
  }
  &__nav {
    display: none;
    @include screen(tablet) {
      display: flex;
      align-items: center;
      gap: 24px;
    }
  }
  &__link {
    text-transform: uppercase;
    font-weight: 500;
    display: inline-block;
    font-variant-numeric: lining-nums;
    white-space: nowrap;
    font-size: 22px;
    line-height: 20px;
    @include screen(laptop) {
      font-size: 13px;
      transition: color $transition-default;
      &:hover {
        color: $primary;
      }
    }
    @include screen(desktop) {
      font-size: 14px;
    }
  }
  &__catalog {
    display: none;
    @include screen(laptop) {
      display: block;
    }
  }
  &__burger {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    svg {
      width: 18px;
      height: 12px;
      path,
      rect {
        transition: transform $transition-default, opacity $transition-default;
      }
    }
    &.active {
      svg {
        rect {
          &:nth-child(1) {
            transform: translateX(3px) rotate(45deg);
          }
          &:nth-child(2) {
            opacity: 0;
          }
        }
        path {
          &:nth-child(3) {
            transform: translateX(-7px) translateY(3px) rotate(-45deg);
          }
        }
      }
    }
    @include screen(laptop) {
      display: none;
    }
  }
  &__mobile {
    @include screen(laptop) {
      display: none;
    }
  }
  &__phone {
    margin-top: 32px;
    font-size: 26px;
    @include screen(laptop) {
      font-size: 13px;
      margin-top: 0;
    }
  }
}