.products {
  padding: 30px 0;
  @include screen(tablet) {
    padding: 60px 0;
  }
  &__heading {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    display: grid;
    gap: 16px;
    h2 {
      @include screen(tablet) {
        grid-column: span 4 / span 4;
      }
    }
    &__content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      p {
        font-weight: 500;
        font-size: 22px;
        line-height: 26px;
        letter-spacing: -0.01em;
        @include screen(tablet) {
          font-size: 36px;
          line-height: 43px;
          margin-bottom: 8px;
        }
      }
      span {
        display: inline-block;
        font-size: 16px;
        line-height: 22px;
        margin-top: 16px;
        font-weight: 500;
        letter-spacing: -0.01em;
        @include screen(tablet) {
          line-height: 28px;
          font-size: 24px;
        }
      }
      @include screen(tablet) {
        grid-column: span 7 / span 7;
      }
    }
    @include screen(tablet) {
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__items {
    margin-top: 32px;
    display: grid;
    gap: 8px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    @include screen(tablet) {
      margin-top: 80px;
      gap: 32px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__item {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    min-height: 280px;
    max-height: 280px;
    color: $white;
    &:before {
      content: '';
      background: linear-gradient(90deg, #1F1F1F -15.89%, rgba(31, 31, 31, 0.7) 50.47%, rgba(31, 31, 31, 0) 94.7%);
      position: absolute;
      left: 0;
      height: 100%;
      top: 0;
      width: 100%;
      z-index: 2;
      @include screen(laptop) {
        transition: width $transition-default;
      }
    }
    @include screen(tablet) {
      max-height: 100%;
    }
    @include screen(laptop) {
      &:hover {
        &:before {
          width: 200%;
        }
      }
    }
    &:nth-child(1) {
      @include screen(tablet) {
        grid-row-start: 1;
        grid-row-end: 3;
        grid-column: span 1;
      }
    }
  }
  &__bg {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title {
    font-size: 22px;
    font-weight: 500;
    text-align: left;
    line-height: 26px;
    margin-bottom: 16px;
    @include screen(tablet) {
      font-size: 36px;
      line-height: 43px;
      margin-bottom: 12px;
    }
  }
  &__text {
    text-align: left;
  }
  &__content {
    padding: 24px 24px 0 24px;
    top: 0;
    left: 0;
    width: 100%;
    position: absolute;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include screen(tablet) {
      padding: 40px 40px 0 40px;
    }
  }
  &__footer {
    padding: 0px 24px 24px 24px;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    z-index: 2;
    @include screen(tablet) {
      padding: 0px 40px 40px 40px;
    }
  }
  &__price {
    font-weight: 500;
    letter-spacing: -0.01em;
    font-size: 16px;
    line-height: 22px;
    @include screen(tablet) {
      font-size: 20px;
      line-height: 24px;
    }
    strong {
      font-size: 16px;
      line-height: 22px;
      font-weight: 500;
      @include screen(tablet) {
        font-size: 26px;
        line-height: 31px;
      }
    }
  }
  &__button {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 14px;
    bottom: 14px;
    @include screen(tablet) {
      width: 80px;
      height: 80px;
      right: 30px;
      bottom: 30px;
    }
  }
}