.materials {
  padding: 30px 0;
  @include screen(tablet) {
    padding: 60px 0;
  }
  &__heading {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    display: grid;
    gap: 16px;
    h2 {
      @include screen(tablet) {
        grid-column: span 5 / span 5;
      }
    }
    p {
      font-weight: 500;
      font-size: 16px;
      line-height: 22px;
      letter-spacing: -0.01em;
      @include screen(tablet) {
        font-size: 24px;
        line-height: 29px;
        grid-column: span 7 / span 7;
      }
    }
    @include screen(tablet) {
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__items {
    display: grid;
    margin-top: 24px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 9px;
    @include screen(tablet) {
      margin-top: 48px;
      grid-template-columns: repeat(5, minmax(0, 1fr));
      gap: 32px;
    }
  }
  &__item {
    border: 1px solid rgba($dark, 0.24);
    border-radius: 10px;
    padding: 8px;
    @include screen(tablet) {
      padding: 16px;
    }
  }
  &__image {
    margin-top: 12px;
    border-radius: 8px;
    img {
      object-fit: contain;
      width: 100%;
    }
  }
  &__title {
    text-align: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: -0.01em;
    text-transform: uppercase;
    @include screen(tablet) {
      font-size: 22px;
      line-height: 26px;
    }
  }
  &__color {
    text-align: center;
    font-weight: 500;
    margin-top: 4px;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: -0.01em;
  }
}